.main-header {
  padding-top: 100px;
  position: absolute;
  top: 0;
  left: 0;
  right: 75%;
  z-index: 3;

  .page-logo {
    position: absolute;
    top: 20px;
    left: 20px;
    img {
      max-height: 50px; } }

  &-nav {
    padding: {
      left: $navPaddingLeft; }

    &--item {
      height: 3em;
      a {
        position: relative;
        text-decoration: none;
        font-weight: 500;
        font-size: 1.6em;
        color: white;
        line-height: 1.6em;
        cursor: pointer;

        &:hover, &:active, &:focus, &.active {
          border-bottom: 1px solid white; }
        &.active::before {
          content: '>';
          font-size: 1.15em;
          display: inline-block;
          position: absolute;
          left: -0.9em; } } } } }

@media screen and (max-height: 850px) {
  .main-header {
    padding-top: 10px;

    &-nav {
      padding-left: $navPaddingLeft-heightAdjusted; } } }
